export default{
	data(){
		return{
			yonghuming:'',
			mima:'',
			yonghuid:'',
			a:'',
			b:''
		}	
	},
	onLoad:function(){
		var that=this;
		uni.getStorage({
			key:"storage_name",
			success:function(res){
				that.yonghuming=res.data;
			}
		})
		uni.getStorage({
			key:"storage_pwd",
			success:function(res){
				that.mima=res.data;
			}
		})
		uni.getStorage({
			key:"storage_nameCode",
			success:function(res){
				that.yonghuid=res.data;
			}
		})
	},
	methods:{
		wancheng:function(){
			// 加判断
			var _self=this;
			uni.request({
				url:"http://192.168.1.143:8080/Users/updatePassByUserCode",
				method:"POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded', 
				},
				data:{userCode:_self.yonghuid,passWord:_self.a},
				success:function(res){
					console.log(res);
					uni.reLaunch({
					    url: "../login/login"
					});
				}
			})
		}
		
	}
}
	
	
